	module.exports = {"data":{"site":{"siteMetadata":{"title":"Yusuke Shibata"}},"markdownRemark":{"excerpt":"…","htmlAst":{"type":"root","children":[{"type":"element","tagName":"h3","properties":{},"children":[{"type":"text","value":"現状について"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"頭の中がこんがらがって、混乱している。\nこれからどうしたらいいんだろうとか、自分に自信がないとか、いつものことだけど。\n歯車がちょっとガチャガチャしていて現状を見直そうとおもう。\n整理の意味を込めて今考えていることや、\n悩んでいること、色々雑多に書いていってみよう。"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"そういや昨日読んだ英語の文章に自分の行動、目標にはきっちりとしたevidenceを自分に提示してから動いたほうがいいと書いてあったな、自分はなんとなくで生きすぎ。"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"資本主義のこの世の中では、カネのことくらいうまくいきていかなきゃ。\nもっと価値のある悩みをちゃんとしよう。"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h3","properties":{},"children":[{"type":"text","value":"めぐ"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"毎日頑張っている。"},{"type":"element","tagName":"br","properties":{},"children":[]},{"type":"text","value":"\n一生懸命頑張っている。"},{"type":"element","tagName":"br","properties":{},"children":[]},{"type":"text","value":"\nめぐのために頑張っている。  "}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"めぐの笑顔を見るといつでもどんな苦労でも乗り越えられるって気持ちになる。\nあの人のために人生を捧げられていることは僕にとっての本当の幸せだといつも感じる。"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h3","properties":{},"children":[{"type":"text","value":"いまニューヨークに居る"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"到着して三日目。まだjet lagに苦しめられている。\nついていきなり雪がすごい降った。\n今からジョギングしていろいろ考えよう。"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"今日は夜ホストのミュージシャンのお兄さんのコンサート(?)があるのでよってみる。"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"h4","properties":{},"children":[{"type":"text","value":"ニューヨーク滞在の目標"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"ul","properties":{},"children":[{"type":"text","value":"\n"},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"毎日ジョギングして考えてちゃんと文章にする(これからの人生と自分にしかできないことを明確にする)"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"Githubで1000starをget(Programmerとしての今後のための努力)"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"TOEFLで100点以上取る(きっちり英語を聞いて読んで、模擬試験も自分でやる)"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"自分のVISA発行のためにちゃんと行動"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"金を稼ぐこと、アメリカに住むこと、いろいろと優先順位を決定する"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"(ダイエット)"}]},{"type":"text","value":"\n"}]}],"data":{"quirksMode":false}},"frontmatter":{"title":"現状","subtitle":"","date":"08 March, 2018"}}},"pathContext":{"slug":"2018-03-08"}}

